/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*
  2. Remove default margin
*/
* {
  margin: 0;
  padding: 0;
}

/*
  Typographic tweaks!
  3. Add accessible line-height
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

/*
  5. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/*
  6. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}

/*
  7. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* fonts */
.poppins-extralight {
  font-family: "Poppins", sans-serif;
  font-weight: 200;
  font-style: normal;
}

.poppins-regular {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.poppins-medium {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.poppins-semibold {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
}

.poppins-bold {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.poppins-extrabold {
  font-family: "Poppins", sans-serif;
  font-weight: 800;
  font-style: normal;
}

.poppins-black {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  font-style: normal;
}

.poppins-regular-italic {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-style: italic;
}

/* Custom properties */
:root {
  --nav-color: #3e2248ff;
  --english-violet: #5b3f64ff;
  --dark-purple-2: #3e2248ff;
  --chinese-violet: #775c7fff;
  --mountbatten-pink: #93799aff;
  --lilac: #b096b6ff;
  --thistle: #ccb3d1ff;
}

/* Navbar */
.navigation-bar {
  background-color: var(--nav-color);
  padding: 12px 16px;
}

.search-box {
  border-radius: 12px;
  border: 1px solid #ffffff1f;
  padding: 4px 16px;
  background-color: var(--english-violet);
  width: 30%;
  display: inline-block;
}

.search-box:focus-within {
  background-color: #ffffff0f;
  border: 1px solid #ffffff5f;
}

.search-form {
  width: 100%;
  display: flex;
  align-items: center;
}

#location {
  outline: none;
  background-color: inherit;
  border: none;
  color: #fff;
  flex: 1;
  width: 100%;
}

.btn {
  cursor: pointer;
}

#search-btn {
  border: none;
  color: rgba(255, 255, 255, 1);
}

.toggle-unit {
  border-radius: 12px;
  border: 1px solid #ffffff1f;
  padding: 4px 16px;
  background-color: var(--english-violet);
  color: #fff;
  display: inline;
}

.toggle-unit * {
  margin: 0.5rem;
}

.toggle-unit:hover {
  background-color: #ffffff0f;
  border: 1px solid #ffffff5f;
}

.select-unit {
  font-weight: 700;
  text-decoration: underline;
}

/* Main section */
main {
  background: linear-gradient(
    180deg,
    hsla(284, 36%, 21%, 1) 0%,
    hsla(285, 23%, 32%, 1) 23%,
    hsla(286, 16%, 43%, 1) 72%
  );
  height: 95vh;
  padding: 12px 16px;
}

.card {
  background-color: var(--thistle);
  border-radius: 12px;
  padding: 12px 16px;
}

.temp-data {
  font-size: 4rem;
}

.location-icon {
  font-size: 16px;
}

.location-container {
  display: flex;
  align-items: center;
}

.conditions-data {
  font-size: 1.2rem;
}

.main-data-container {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.misc-data {
  display: flex;
  gap: 8px;
  justify-content: space-between;
  margin-top: 16px;
  flex-wrap: wrap;
}

.misc-data > div {
  display: flex;
  flex-direction: column;
}

.data-heading {
  font-weight: 700;
}

.forecast-heading {
  margin: 16px 0 4px;
  color: #fff;
}

.forecast-container {
  display: flex;
  width: 100%;
  gap: 4px;
  overflow-x: auto;
}

.forecast-card {
  background-color: var(--thistle);
  border-radius: 12px;
  padding: 12px 16px;
  flex-shrink: 0;
  margin-bottom: 8px;
  flex-basis: 150px;
}

.forecast-data-container {
  display: flex;
  align-items: center;
  gap: 1rem;
}
